// Variables:
@primary: #2E9CCD;
@red: #E47475;
@yellow: rgba(250, 162, 45, 0.8);
@blue: rgba(104, 211, 210, 0.85);
@green: rgba(104, 211, 210, 0.85);

.common-font {
    font-size: 18px;
    line-height: 21px;
    font-style: normal;
    font-family: Rubik;
    font-weight: normal;
}

// Buttons:
.primary-button {
    border: none;
    outline: none;
    padding: 10px;
    .common-font();
    color: white;
    cursor: pointer;
    background: none;
    border-radius: 5px;
    padding-left: 25px;
    padding-right: 25px;
    text-decoration: none;
    background-color: @primary;

    // Hover and click states.
    &:hover {
        background-color: darken(@primary, 10%);
    }
    &:active {
        background-color: darken(@primary, 15%);
    }
}

// The entire app has the loose leaf paper background.
.app {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-size: 100%;
    background-repeat: repeat-y;
    background-image: url('../images//LooseLeafBackground.svg');
}